import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import ViewColumn from "@material-ui/icons/ViewColumn";

import { Column } from "material-table";
import { DropdownDesignPropertyProperty } from "store/objects/design-property";

export const tableIcons = {
    Add: forwardRef<SVGSVGElement>((props, ref) => <AddToPhotosOutlinedIcon {...props} ref={ref} />),
    Check: forwardRef<SVGSVGElement>((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef<SVGSVGElement>((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef<SVGSVGElement>((props, ref) => <DeleteOutlined {...props} ref={ref} />),
    DetailPanel: forwardRef<SVGSVGElement>((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef<SVGSVGElement>((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef<SVGSVGElement>((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef<SVGSVGElement>((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef<SVGSVGElement>((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef<SVGSVGElement>((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef<SVGSVGElement>((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef<SVGSVGElement>((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef<SVGSVGElement>((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef<SVGSVGElement>((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef<SVGSVGElement>((props, ref) => <ArrowDownward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef<SVGSVGElement>((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef<SVGSVGElement>((props, ref) => <ViewColumn {...props} ref={ref} />),
};

export interface TableState {
    columns: Array<Column<DropdownDesignPropertyProperty>>;
    data: DropdownDesignPropertyProperty[];
}

export interface TableEdit {
    isEditable?: (rowData: DropdownDesignPropertyProperty) => boolean;
    isDeletable?: (rowData: DropdownDesignPropertyProperty) => boolean;
    onRowAdd?: (newData: DropdownDesignPropertyProperty) => Promise<any>;
    onRowUpdate?: (newData: DropdownDesignPropertyProperty, oldData?: DropdownDesignPropertyProperty) => Promise<any>;
    onRowDelete?: (oldData: DropdownDesignPropertyProperty) => Promise<any>;
    editTooltip?: (rowData: DropdownDesignPropertyProperty) => string;
    deleteTooltip?: (rowData: DropdownDesignPropertyProperty) => string;
    onRowAddCancelled?: (rowData: DropdownDesignPropertyProperty) => void;
    onRowUpdateCancelled?: (rowData: DropdownDesignPropertyProperty) => void;
    isEditHidden?: (rowData: DropdownDesignPropertyProperty) => boolean;
    isDeleteHidden?: (rowData: DropdownDesignPropertyProperty) => boolean;
}
